
.plans_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 50px 10px;
}

.plans_title {
    font-size: 40px;
    font-weight: bold;
    color: #171A1F;
}

.plans_description {
    color: #323842;
    margin-bottom: 20px;
    text-align: center;
}

.plans_switch {
    background-color: #00BDD6;
    width: 200px;
    height: 45px;
    border-radius: 9px;
    display: flex;
    justify-content: center;
    gap: 3px;
    align-items: center;
}


.monthly_button,
.yearly_button {
    width: 95px;
    height: 38px;
    border: none;
    outline: none;
    border-radius: 9px;
    cursor: pointer;
    background: transparent;
    color: #ffffff;
}

.monthly_button.active,
.yearly_button.active {
    background-color: #ffffff;
    color: #00BDD6;
}

.plan_box_container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    gap: 20px;
    height: 600px;
    width: 100vw;
}

.plan_box {
    width: 300px;
    height: 380px;
    background-color: #F8F9FA;
    padding: 20px;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
}

.plan_box_title {
    font-size: 26px;
    font-weight: bold;
    color: #171A1F;
}

.plan_box_description {
    color: #424955;
}

.plan_box_cost {
    font-size: 32px;
    font-weight: bold;
    margin: 5px 0px 15px 0px;
    
}

.plan_duration {
    font-size: 14px;
    font-weight:300;
    color: #424955;
}

.feature_list {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    color: #171A1F;
    margin-bottom: 10px;
}

.tick_icon {
    font-size: 18px;
    color: #1DD75B;
}

.close_icon {
    font-size: 18px;
    color: #424955;
}

.plan_button {
    width: 100%;
    height: 40px;
    margin-top: 15px;
    outline: none;
    border: 1px solid #00BDD6;
    color: #00BDD6;
    background-color: #ffffff;
    font-size: 14px;
}

.mail_icon {
    margin-top: 5px;
    font-size: 16px;
}

.plan_box.selected {
    background-color: #ffffff;
    height: 400px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.plan_box.selected .plan_button {
    background-color: #00BDD6;
    color: #ffffff;
}

@media screen and (max-width:1100px) {
    .plan_box_container {
        width: 80%;
    }
}

@media screen and (max-width: 950px) {
    .plan_box_container {
        flex-direction: column;
        width: 100%;
        height: fit-content;
    }

    
}

@media screen and (max-width: 500px) {
    .plan_box_container {
        flex-direction: column;
        width: 300px;
        padding:20px;
        height: fit-content;
    }
}

