
.clients_content {
    margin: 100px 0 70px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.clients_left {
    width: 500px;
}

.clients_title {
    font-size: 42px;
    font-weight: bold;
    color: #323842;
}

.clients_description {
    color: #323842;
    margin-top: 10px;
}

.clients button {
    width: 150px;
    height: 42px;
    border-radius: 18px;
    background-color: #00BDD6;
    color: #ffffff;
    border: none;
    cursor: pointer;
    font-size: 16px;
    margin-top: 20px;
}

.clients button:hover {
    background-color: #33CCE4;
}

.client {
    width: 142px;
    height: 87px;
}

@media screen and (max-width:1250px) {
    .clients_content {
        flex-direction: column;
        text-align: center;
    }

    .clients_title {
        font-size: 38px;
    }

    .clients_description {
        font-size: 14px;
    }

}

@media screen and (max-width: 600px) {
    .clients_left{
        padding: 20px;
    }

    .clients_description {
        width: 80%;
        margin: 0 auto;
    }
    
    .clients_title {
        font-size: 32px;
    }
}