.mission_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    background-color: #FAFAFB;
    padding:100px 0px;
}

.mission_content {
    width: 570px;
    z-index: 1;
    padding: 50px;
}

.mission_description {
    font-size:40px;
    color: #323842;
    
    
}

.mission_container .small_title {
    color: #323842;
    font-style: italic;
}

.quote {
    position: relative;
    left: -140px;
    bottom: -80px;
}

@media screen and (max-width: 950px) {
    .mission_description {
        font-size:36px;
    }
}