/* HOME FEATURES */

* {
    overflow-x: hidden;
}
.features {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.features h1 {
    color: #323842;
    font-size: 42px;
}
.home_feature {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.feature {
    width:400px;
    height: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    overflow-x: hidden;
}

.feature-img {
    width: 172px;
    height: 172px;
}

.feature_title {
    font-size: 28px;
    font-weight: bold;
    color: #323842;
    margin-top: 5px;
}

.feature p {
    color: #171A1F;
    margin-top: 10px;
}

.feature_button button {
    width: 200px;
    height: 40px;
    border-radius: 26px;
    border: 1px solid #00BDD6;
    color: #00BDD6;
    background-color: transparent;
    cursor: pointer;
    font-size: 16px;
}

.feature_button button:hover {
    background-color: #00BDD6;
    color: #ffffff;
}

@media screen and (max-width: 1250px){
    .feature{
        width: 300px;
    }

    .home_feature{
        display: flex;
        flex-direction: column;
    }

    .features_title {
        font-size: 40px;
    }

    .features_description {
        font-size: 14px;
    }
}

@media screen and (max-width: 650px){

    .features_title {
        font-size: 36px;
    }

    .features_description {
        font-size: 14px;
    }
}