.testimonial_bg {
    background-image: url('../Images/slider_bg.png');
    width: 100vw;
    height: 382px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: -100;
}

.testimonial_title {
    font-size: 48px;
    color: #ffffff;
    font-weight: bold;
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 75px;
}

.testimonial {
    height: 500px;
    overflow: hidden;
}

@media screen and (max-width: 720px) {
    .testimonial_title {
        font-size: 40px;
    }
}
