.navbar_container {
    width: 100vw;
    height: 150px;
    background-color: #323842;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.social_icons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.social_icon {
    font-size: 26px;
    cursor:pointer;
}

a {
    text-decoration: none;
    color: inherit;
}

