* {
    margin: 0px;
    padding: 0px;
}

.container {
    margin: 0 auto;
    padding: 40px;
    position: relative;
    top: -200px;
}


.profile {
    display: flex;
    align-items: center;
    gap: 10px;
}

.profile_img img {
    
    width: 72px;
    height: 72px;
    border-radius: 50%;
    object-fit: cover;
    
}
.user_review {
    margin: 20px 0px;
    width: 350px;
}



.slick-slide > div {
    margin:0 10px;
    background-color: #ffffff;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 16px;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 30px;
    margin-bottom: 10px;
}

.star {
    color: #FFD700;
    font-size: 30px ;
}


@media screen and (max-width: 450px) {
    .slick-slide > div {
        padding:0px 10px;
    }
}