.contact_container {
    display: flex;
    justify-content:center;
    align-items: center;
    width: 100vw;
    
    margin: 80px 0px ;
}

.contact_left {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact_right {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact_title {
    font-size: 40px;
    font-weight: bold;
}

.contact_description {
    color: #9095A0;
    margin: 10px 0px 30px 0px;
}

.contact_img {
    height: 480px;
}


.contact_box {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin-top: 20px;
}

.contact_form {
    width: 100%;
}

.contact_form .name {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    max-width: 600px;
}

.firstname {
    width: 100%;
}

.lastname {
    width: 100%;
}

.inputbox {
    border-radius: 4px;
    outline: none;
    border: 1px solid #9095A0;
    padding: 10px 20px;
    height: 30px;
    font-size: 16px;
}

label {
    font-size: 16px;
    font-weight: bold;
    color: #424955;
}

.textbox {
    height: 120px;
    border-radius: 4px;
    border:1px solid #9095A0;
}

.contact_button {
    background-color: #00BDD6;
    height: 52px;
    color: #ffffff;
    outline: none;
    border-radius: 4px;
    border: none;
    font-size: 16px;
}

input::placeholder {
    color: #BCC1CA;
}

textarea {
    padding: 10px 20px;
    font-size: 16px;
    outline: none;
    font-family: 'Poppins';
}

textarea::placeholder {
    color: #BCC1CA;
}

@media screen and (max-width:1000px) {
    .contact_container {
        flex-direction: column;
    }

    .contact_left {
        width: 100%;
        padding: 10px;
    }

    .contact_right {
        width: 100%;
        
    }
    .contact_right > div {
        padding: 10px;
    }

    .contact_title {
        font-size: 34px;
    }
}