.about_us_container {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    padding: 50px 0px;
    overflow: hidden;
    
   
}

.about_us_image {
    width: 500px;
    height: 300px;
    overflow: hidden;
}

.about_us_image > img {
    width:100%;
    height: 100%;
    border-radius: 20px;
}

.about_us_content {
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about_us_right {
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.about_us_container .about_us_title {
    font-size: 42px;
    color: #323842;
    font-weight: bold;
    margin-bottom: 10px;
}

.about_us_content .about_us_description {
    width: 450px;
    color: #171A1F;
}

.up_arrow {
    position: absolute;
    top: 0px;
    right: 80px;
    z-index: -1;
}

.down_arrow {
    position: absolute;
    bottom: 0px;
    left: 80px;
    z-index: -1;
}

@media screen and (max-width:1100px) {
    .about_us_container {
        flex-direction: column;
    }

    .about_us_content {
        text-align: center;
        margin:40px 0px ;
    }

    .arrow {
        display: none;
    }


}

@media screen and (max-width:550px) {
    .about_us_content .about_us_description {
        width: 300px;
    }

    .about_us_right {
        display: none;
    }
}

