
/* HOME MAIN PAGE*/

.home {
    display: flex;
    align-items: center;
    margin: 0 150px;
    height: 100vh;
}


.home h1 {
    width: 450px;
    font-weight: bold;
    font-size: 48px;
    color: #323842;
    margin-bottom: 15px;
}

.home p {
    width: 500px;
    margin-bottom: 15px;
}

.home_buttons {
    width: 500px;
}

.primary {
    width: 150px;
    height: 42px;
    border-radius: 18px;
    background-color: #00BDD6;
    color: #ffffff;
    border: none;
    cursor: pointer;
    font-size: 16px;
    margin-right: 10px;
}

.secondary {
    width: 150px;
    height: 42px;
    border-radius: 18px;
    color: #9095A0;
    border: none;
    cursor: pointer;
    font-size: 16px;
    background-color: transparent;
}

.primary:hover {
    background-color: #33CCE4;
}

.secondary:hover {
    background-color: transparent;
}

.home_images {
    overflow: hidden;
    display: block;
}

.img_laptop {
    width: 154px;
    height: 156px;
    border-radius: 16px;
    position: absolute;
    top: 124px;
    left: 894px;
}

.img_rate {
    width: 268px;
    height: 140px;
    position: absolute;
    top: 124px;
    left: 1068px;
}

.img_progress {
    width:288px ;
    height: 298px;
    position: absolute;
    top: 300px;
    left: 846px;
}

.img_mobile {
    width: 180px;
    height: 213px;
    position: absolute;
    top: 280px;
    left: 1158px;
}

.img_arc_up {
    width: 320px;
    height: 96px;
    position: absolute;
    left: 650px;
    top: 38px;
}

.img_arc_down {
    width: 349px;
    height: 129px;
    position: absolute;
    left: 1070px;
    top: 580px;
}




@media screen and (max-width: 1450px) {
    .home {
        margin: 0 100px;
    }

    .img_laptop {
        left: 694px;
    }

    .img_rate {
        left: 878px;
    }
    
    .img_progress {
        left: 656px;
    }
    
    .img_mobile {
        left: 968px;
    }
    
    .img_arc_up {
        left: 460px;
    }
    
    .img_arc_down {
        left: 880px;
    }
}

@media screen and (max-width: 1250px) {
    .home_images {
        display: none;
    }

    .home_buttons {
        text-align: center;
    }

    .home {
        justify-content: center;
        align-items: center;
        margin: 0px;
    }

    .home h1 {
        width: 400px;
        text-align: center;
    }
    
    .home p {
        width: 700px;
        text-align: center;
    }

    .home_content{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    

}

@media screen and (max-width:650px) {
    .home h1 {
        width: 400px;
        text-align: center;
        font-size: 40px;
    }
    
    .home p {
        width: 400px;
        text-align: center;
        font-size: 14px;
    }

    .primary{
        font-size: 14px;
    }
    
    .secondary {
        font-size: 14px;
    }
}

