.faq_container {
    display: flex;
    align-items: center;
    padding: 20px 0px 0px 120px;
    background-color: #FAFAFB;
    height: 760px;
}

.left {
    width: 40%;
    display: flex;
    justify-content: center;

}

.small_title {
    font-size: 14px;
    color: #0095A9;
}

.title {
    font-size: 35px;
    font-weight: bold;
    color: #323842;
    margin-bottom: 10px;
}

.faq_box {
    width: 600px;;
    height:fit-content;
    padding: 20px;
    border-bottom: 1px solid #EAECEF;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.faq_active {
    width: 600px;
    height:fit-content;
    padding: 20px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    border-radius: 8px;

}

.faq_icon .faq_down {
    width: 35px;
    height: 35px;
    color: #00BDD6;
}

.faq_qa {
    width: 80%;
}
.answer {
    margin-top: 5px;
    color: #00BDD6;
    font-size: 16px;
    line-height: 1.5;
    display: none;
    
}

.question {
    font-size: 20px;
    font-weight: 400px;
    color: #565E6C;
    
}

.faq_active .question{
    color: #0095A9;
    font-weight: 500;
}

.faq_active .answer{
    display: block;
    
}

.box {
    cursor: pointer;
}

@media screen and (max-width: 1225px) {
    .left {
        display: none;
    }

    .faq_container {
        justify-content: center;
        padding: 30px;
    }
}

@media screen and (max-width: 650px) {
    .small_title {
        font-size: 12px;
    }
    
    .title {
        font-size: 28px;
    }

    .answer {
        font-size: 12px;
    }
    
    .question {
        font-size: 16px;
        font-weight: 400px;
        color: #565E6C;
    }

    .faq_box {
        width: 350px;
    }

    .faq_active {
        width: 350px;
    }
    
}

@media screen and (max-width:420px) {
    .faq_box {
        width: 250px;
    }

    .faq_active {
        width: 250px;
    }
}