.course_container {
    display: flex;
    gap: 20px;
    text-align: center;
    width: 100vw;
    padding: 20px;
    margin-bottom: 100px;
}

.course_image > img {
    width: 200px;
    height: 200px;
}

.course_title {
    font-size: 24px;
    color: #171A1F;
    margin-bottom: 5px;
}

.course_description {
    color: #171A1F;
    margin-bottom: 10px;
    font-size: 14px;
}

.course_button {
    width: 100%;
    height: 40px;
    background-color: #00BDD6;
    color: #ffffff;
    outline: none;
    border: none;
}

.course {
    border: 1px solid #BCC1CA;
    padding: 20px;
    border-radius: 10px;
    width: 350px;
    height: fit-content;
}

.course_button:hover {
    background-color: #65cad7;
}

.course_main_title {
    color: #171A1F;
    font-size: 30px;
    padding: 50px 20px 0px 20px;
    font-weight: bold;
}


@media screen and (max-width: 1260px) {
    .course {
        width: 250px;
    }

    .course_image > img {
        width: 150px;
        height: 150px;
    }
}

@media screen and (max-width: 960px) {
    .course_container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .course-main-title {
        text-align: center;
    }

    .course {
        width: 500px;
        height :fit-content;
        
    }
}

@media screen and (max-width:600px) {
    .course {
        width: 250px;
    }

    .course_image > img {
        width: 150px;
        height: 150px;
    }
}