.newsletter_container {
    height: 400px;
    background-color: #FAFAFB;
}

.newsletter_bg {
    width: 75%;
    height: 250px;
    background-color: #0095A9;
    margin: 0 auto;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
}

.newsletter_title {
    font-size: 30px;
    font-weight: bold;
    color: #ffffff;
}

.newsletter_description {
    font-size: 16px;
    color: #ffffff;
}

.newsletter_input{
    display: flex;
    gap: 10px;
    margin-top: 10px;
}

.email_input {
    width: 100%;
    height: 40px;
    border-radius: 8px;
    font-size: 14px;
    padding: 5px 10px;
    box-sizing: border-box;
    background-color: #ffffff;
    border: none;
    outline: none;
    color: #00DCF9;
}

.newsletter_button {
    height: 40px;
    width: 120px;
    background-color: #8353E2;
    border-radius: 8px;
    outline: none;
    border: none;
    color: #ffffff;
}

.newsletter_button:hover {
    background-color: #a283e1;
}

.email_input::placeholder {
    color: #00DCF9;
}

@media screen and (max-width:650px) {
    .newsletter_input {
        flex-direction: column;
        align-items: center;
    }

    .newsletter_title {
        font-size: 26px;
    }
    
    .newsletter_description {
        font-size: 12px;
    }

}

@media screen and (max-width: 400px) {
    .newsletter_title {
        text-align: center;
    }
}