.navbar {
    display: flex;
    height: 64px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
    align-items: center;
    padding:0 50px 0 50px;
    justify-content: space-between;
    box-sizing: border-box;
}   

.navbar_mobile_active {
    position: fixed;
    width: 100%;
    background-color: #ffffff;
    z-index: 1;
}

.links {
    display: flex;
    gap: 40px;
}

.navbar_links_active {
    display: block;
}

li {
    list-style: none;
    text-decoration: none;
    
}

a {
    text-decoration: none;
    color: #505050;
}

.navbar button {
    width: 101px;
    height: 36px;
    border-radius: 18px;
    background-color: #00BDD6;
    color: #ffffff;
    border: none;
    cursor: pointer;
    font-size: 14;
    
}

.navbar button:hover {
    background-color: #33CCE4;
}

.navbar_button button {
    display: block;
}

.navbar_logo img {
    width: 106px;
    height: 40px;
    cursor: pointer;
}


.icon {
    color: #000000;
    width: 25px;
    height: 25px;
}

.mobile {
    display: none;
}

.links button {
    display: none;
}

.line{
    height: 2px;
    width: 0%;
    background-color: #00BDD6;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s ease-in ;
}

.lines:hover .line{
    visibility: visible;
    opacity: 1;
    width: 100%;
}




@media screen and (max-width: 769px){

    .navbar_links {
        display: none;
        
    }
    
    .links {
        display: flex;
        flex-direction: column;
        gap: 40px;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 65px;
        right:0px;
        width: 300px;
        height: 100vh;
        background: #2a3239;
        box-shadow: 0 40px 60px rgba(0, 0, 0, 0, 0.1);
        padding: 40px 0 0 30px;
        color: #ffffff;
    }

    a {
        color: #ffffff;
    }

    .mobile {
        display: block;
    }

    .links button {
        display: block;
    }

    .navbar_button button {
        display: none;
    }

    
}

@media screen and (max-width: 450px) {
    .navbar {
        padding:0 20px 0 20px;
    }
}

